.playerHand{display:flex;justify-content:center;align-items:center;height:25%;width:50%}.playerHand:nth-of-type(even){flex-flow:row-reverse}.playerHand>*{flex-shrink:0}.avatar{display:flex;flex-flow:column;align-items:center;width:10em;margin:0 1.5em}.avatar img{height:5rem;width:5rem;margin:.5em;border-radius:50%;border:.25em solid #2e2e2e}.avatar div{font-size:1.25rem;line-height:1.5em;max-width:100%;overflow:hidden;white-space:nowrap;text-overflow:ellipsis}.lives{display:flex}.lives div::after{content:"✪";margin:0 .125em;color:#7e5701}.backOfCard{position:relative;height:10em;width:8em;font-size:.75rem;border-radius:1.5em;box-shadow:0 0 0 .25em #fff,0 0 0 .625em #c8c8a2,0 0 0 1em #fff;background:repeating-conic-gradient(#7b7b65 0% 25%, #f5f5db 0% 50%) 50%/2em 2em}